@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap');

$primary: #2081e2; 
$black: #222222; 
$grey: #717171; 
$white: #ffffff; 
$green: #65c482; 
$grey-2: #fbfdff; 
$title-32bold: 700 2rem/2.25rem Inter; 
$title-45bold: 700 2.8125rem/normal Inter; 
$subtitle-22medium: 500 1.375rem/1.625rem Inter; 
$subtitle-22bold: 700 1.375rem/1.625rem Inter; 
$body-16regular: 400 1rem/1.25rem Inter; 
$body-16bold: 700 1rem/1.25rem Inter; 
$body-14regular: 400 0.875rem/1.125rem Inter; 
$body-14regular_u: 500 0.875rem/1.25rem Inter; 
$caption-12bold: 700 0.75rem/1rem Inter; 


body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: var(--body-14regular);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
